.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

h1, p, ul, li, button {
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
}

ul {
  list-style: none;
}

button {
  text-decoration: none;
  border: none;
  background-color: peachpuff;
  padding-top: 15px;
  padding-bottom: 15px;
}

li:first-child {
  padding-top: 15px;
}

li:last-child {
  padding-bottom: 15px;
}

header {
  background-color: paleturquoise;
  height: 15vh;
  color: white;
}

header div {
  height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h1 {
  font-size: 2rem;
}

main {
  background-color: peachpuff;
}

li {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.wrapper {
  max-width: 2000px;
  margin: 0 auto;
  width: 90%;
}

form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 15px
}

footer {
  background-color:pink;
  height: 10vh;
}

footer div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 10vh;
}

footer p {
  font-size: 1rem;
}


